@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  color: var(--text-colour);
  background-color: var(--background-colour); 
}
:root {
    --primary-colour: #104066;
    --secondary-colour: #5B95C3;
    --background-colour: #02233a;

    --button-colour: #fe884e;
    --button-hover: #97A3CD;

    --text-colour: rgb(255, 249, 234);
    --text-hover: rgb(102, 98, 90);
}
:root {
    --header-height: 350px;
}

#header-section {
    /* background-color: var(--primary-colour); */
    height: 350px;
    height: var(--header-height);
    width: 100%;
    line-height: 350px;
    line-height: var(--header-height);
    background-image: url("https://cdn.discordapp.com/attachments/563630157123026945/990363668543856660/test.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    user-select: none;
    -webkit-user-select: none;
}

#website-name {
    font-size: 35px;
    margin: 0 50px;

    font-weight: 900;
}
:root {
    --main-section-padding: 30px;
}
.main-section {
    width: 90%;
    min-height: calc(100vh - var(--header-height) - var(--footer-height) - var(--list-selection-height) - 2*30px);
    min-height: calc(100vh - var(--header-height) - var(--footer-height) - var(--list-selection-height) - 2*var(--main-section-padding));
    padding: 30px 0;
    padding: var(--main-section-padding) 0;    
    display: grid;
    grid-template-columns: repeat(auto-fill, calc(var(--movie-card-width) + 2*var(--movie-card-padding)));
    justify-content: space-evenly;
    grid-gap: 2vw;
    margin: auto;
}
:root {
    --list-selection-height: 100px;
}

.list-selection-bar {
    min-height: 100px;
    min-height: var(--list-selection-height);
    line-height: 100px;
    line-height: var(--list-selection-height);  
    position: relative;
    display: flex;
    flex-wrap: wrap;
    grid-template-columns: auto;
    justify-content: center;
    color: var(--button-text-colour);
}

.add-list-button {
    height: var(--list-card-height);
    width: var(--list-card-height);
    line-height: var(--list-card-height);
    margin: var(--list-card-margin);
    border-radius: 2px;
    text-align: center;
}
.button {
    color: var(--text-colour);
    background-color: var(--button-colour);
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;
}

.button:hover {
    color: var(--text-hover);
    background-color: var(--button-hover);
}
:root {
    --popup-window-width: 500px;
    --popup-window-height: 400px;
    --popup-top-bar-height: 40px;
}
.popup-window {
    background-color: var(--primary-colour);
    width: 500px;
    width: var(--popup-window-width);
    height: 400px;
    height: var(--popup-window-height);
    position: fixed;
    right: calc(50% - 500px/2);
    right: calc(50% - var(--popup-window-width)/2);
    top: calc(50% - 400px/2);
    top: calc(50% - var(--popup-window-height)/2);
    display:none;    
    border-radius: 2px;
    padding: 10px 20px;
    overflow: auto;
}

.popup-top-bar {
    height: 40px;
    height: var(--popup-top-bar-height);
    line-height: 40px;
    line-height: var(--popup-top-bar-height);
    text-align: right;
    -webkit-user-select: none;
            user-select: none;
}

.popup-top-bar div {
    float: right;
    height: 40px;
    font-size: 40px;
    line-height: 40px;
    cursor: pointer;
    display: block;
}
.popup-top-bar div:hover{
    color: var(--text-hover);
}


:root {
    --list-card-height: calc(var(--list-selection-height) * 0.7);
    --list-card-width: 150px;
    --list-card-margin: calc(var(--list-selection-height) * 0.1)
}
.list-card {
    min-width: 150px;
    min-width: var(--list-card-width);
    padding: 0 10px;
    height: calc(var(--list-selection-height) * 0.7);
    height: var(--list-card-height);
    line-height: calc(var(--list-selection-height) * 0.7);
    line-height: var(--list-card-height);
    margin: calc(var(--list-selection-height) * 0.1);
    margin: var(--list-card-margin);
    text-align: center;
    user-select: none;
    -webkit-user-select: none;
    border-radius: 2px;
    cursor: pointer;
}
:root {
    --movie-card-width: 220px;
    --movie-card-padding: 5px;
    --poster-height: calc(var(--movie-card-width) * 1.5);
    --description-height: 120px;
}
.movie-card {
    background: var(--secondary-colour);
    margin: auto;
    width: 220px;
    width: var(--movie-card-width);
    height: calc(calc(220px * 1.5) + 120px);
    height: calc(var(--poster-height) + var(--description-height));
    border-radius: 2px;
    transition: -webkit-transform 0.05s;
    transition: transform 0.05s;
    transition: transform 0.05s, -webkit-transform 0.05s;
    box-shadow: 2px 2px 10px -2px black;
    cursor: pointer;
    padding: 5px;
    padding: var(--movie-card-padding);
}

.movie-card:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03)
}

.movie-poster {
    width: 220px;
    width: var(--movie-card-width);
    height: calc(220px * 1.5);
    height: var(--poster-height);
    line-height: 320px;
    text-align: center;
    border-radius: 2px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.movie-description {
    height: 120px;
    height: var(--description-height);
    width: 100%;
    text-align: center;
}

.add-card {
    text-align: center;
    line-height: calc(calc(220px * 1.5) + 120px);
    line-height: calc(var(--poster-height) + var(--description-height));
    font-size: 80px;
    user-select: none;
    -webkit-user-select: none;
}
.movie-details {
    float: left;
    width: calc(var(--popup-window-width)/2 - 20px);
}

.movie-details-poster {
    width: calc(var(--popup-window-width)/2 - 20px);
    height: calc((var(--popup-window-width)/2 - 20px) * 1.5);
    float: right;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.movie-delete-button {
    position: absolute;
    user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
    height: var(--popup-top-bar-height);
    line-height: var(--popup-top-bar-height);
    margin-top: calc(-1 * var(--popup-top-bar-height))
}

.movie-delete-button:hover {
    color: var(--text-hover);
}
:root {
    --footer-height: 50px;
}
.footer-section {
    background-color: var(--secondary-colour);
    width: 100%;
    min-height: 50px;
    min-height: var(--footer-height);
    line-height: 50px;
    line-height: var(--footer-height);
    text-align: center;
}
.footer-section span {
    margin: 0 5vw;
}

.footer-section a, .footer-section a:visited {
    color: var(--text-colour)
}
:root {
    --settings-button-size: 60px;
}

.settings-button {
    width: 60px;
    width: var(--settings-button-size);
    height: 60px;
    height: var(--settings-button-size);
    line-height: 60px;
    line-height: var(--settings-button-size);
    border-bottom-left-radius: 2px;
    position:fixed;
    top:0;
    right: 0;
    text-align: center;
    user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
}

:root {
    --serach-bar-height: 16px;
}

.settings-menu {
    overflow: auto;
}

.colour-theme {
    border: solid 2px black;
    width: 20px;
    height: 20px;
    display: inline-block;
    margin: 5px;
    cursor: pointer;
}

.light-mode {
    background-color: white;
}
.dark-mode {
    background-color: black;
}

.settings-menu .search-bar {
    height: 16px;
    height: var(--serach-bar-height);
    line-height: 16px;
    line-height: var(--serach-bar-height);
    vertical-align: middle;
}
:root {
    --add-button-width: 150px;
    --add-button-height: 30px;
}
.movie-search-bar {
    height: var(--serach-bar-height);   
    line-height: var(--serach-bar-height);
    vertical-align: middle;
}

.search-button{
    user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
    font-size: 30px;
}

.movie-search-result-container {
    width: 100%;
    margin: 30px auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, 210px);
    justify-content: space-evenly;
    grid-gap: 20px;
    grid-row-gap: 40px;
    row-gap: 40px;
    user-select: none;
    -webkit-user-select: none;
    z-index:9;
}

.movie-search-result {
    background-color: rgb(113, 113, 113);
    height: 300px;
    width: 200px;
    margin: auto;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    z-index: 9;
}
.emptyBar {
    width: 100%;
    height: 20px;
}

.movie-search-result:hover {
    box-shadow: 0px 0px 5px 4px var(--secondary-colour);
}

.add-to-list-button {
    width: 150px;
    width: var(--add-button-width);
    height: 30px;
    height: var(--add-button-height);
    line-height: 30px;
    line-height: var(--add-button-height);
    text-align: center;
    float: right;
    border-radius: 2px;
    z-index: 11;
}

.list-delete-row {
    line-height: 40px;
}
.list-delete-button {
    vertical-align: middle;
    user-select: none;
    -webkit-user-select: none;
    cursor: pointer;
}

.list-delete-button:hover {
    color: var(--text-hover);
}
